<template>
  <div >
      <!-- 按钮操作区 -->
      <el-row type="flex" class="text-left" justify="end">
        <el-col :span="6" style="text-align: left">
          职位名称：<el-input
            v-model="userQuery.positionName"
            clearable
            placeholder="请输入职位名称"
          >
            <el-button
              slot="append"
              @click="query"
              icon="el-icon-search"
            ></el-button>
          </el-input>
        </el-col>
        <el-col  :span="2" :offset="14">
          <el-button
          class="btn"
            @click="newDialog"
            type="primary"
            >新建职位</el-button
          >
        </el-col>
        <el-col  :span="2">
          <el-button class="btn" @click="removePreview" type="danger"
            >删除职位</el-button
          >
        </el-col>
      </el-row>
      <!-- 表单区 （修改tableData）-->
      <el-row>
      <el-table
        @select="rowclick"
        @select-all="rowclick"
        :data="tableData"
        border
        style="width: 100%"
      >
        <el-table-column type="selection" fixed width="55"> </el-table-column>
        <el-table-column type="index" width="55"></el-table-column>
        <el-table-column
          prop="positionName"
          width="150"
          label="职位名称"
        ></el-table-column>
        <el-table-column
          prop="dept"
          label="职能权限涉及部门"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          show-overflow-tooltip
          prop="authority"
          label="权限"
        ></el-table-column>
        <el-table-column label="操作" width="100">
          <template slot-scope="scope">
            <el-button @click="edit(scope)" type="text" size="small"
              >编辑</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      </el-row>
      <!-- 页码条 -->
      <el-row><el-pagination
        layout="total, prev, pager, next, jumper"
        :total="1000"
        :current-page.sync="userQuery.pageNumber"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      >
      </el-pagination></el-row>
      <!-- 新建弹框区 -->
      <el-dialog title="新建职位" :visible.sync="isNewDialogDisplay">
        <div>
          <el-row class="new_row">
            <el-col :span="10" style="line-height: 16px"
              ><p>职位名称</p></el-col
            >
            <el-col :span="10">
              <el-input
                v-model="newDialogPositionName"
                clearable
                placeholder="请输入内容"
              ></el-input>
            </el-col>
          </el-row>
          <el-row>
            <el-tree
              ref="tree"
              :data="data"
              highlight-current
              show-checkbox
              node-key="key"
            ></el-tree>
          </el-row>
          <el-row type="flex" justify="end">
            <el-col :span="4">
              <el-button
                size="medium"
                @click="isNewDialogDisplay = false"
                type="info"
                >取消</el-button
              >
            </el-col>
            <el-col :span="4">
              <el-button size="medium" @click="newDialogConfirm" type="primary"
                >确定</el-button
              >
            </el-col>
          </el-row>
        </div>
      </el-dialog>
      <!-- 删除弹框区 -->
      <el-dialog title="删除职位" :visible.sync="isDeleteDialogDisplay">
        <div style="font-size:18px">即将从系统中删除如下职位，将对如下人员的权限产生影响</div>
        <div class="delete">
          <div>
            <div>职位：</div>
            <div>
              <div v-for="(item, index) in selectData" :key="index">
                {{ item.positionName }}
              </div>
            </div>
          </div>
          <div>
            <div>人员：</div>
            <div>
              <div v-for="(item, index) in selectData" :key="index">
                {{ item.title }}
              </div>
            </div>
          </div>
        </div>
        <el-row type="flex" justify="end">
          <el-col :span="4">
            <el-button
              size="medium"
              @click="isDeleteDialogDisplay = false"
              type="info"
              >取消</el-button
            >
          </el-col>
          <el-col :span="4">
            <el-button size="medium" @click="remove(selectData)" type="primary"
              >确定</el-button
            >
          </el-col>
        </el-row>
      </el-dialog>
  </div>
</template>

<script>
import {
  getAllDeptForms,
  InsertPosition,
  UpdatePosition,
  getAllAuthPosition,
  DeletePosition,
  getSingleAuthPosition,
} from "network/authority.js";
import {
  formname,
  getAllAuthPositionProcess,
  getAllAuthPositionFilter,
  getAllDeptFormsFilter,
  getSingleAuthPositionFilter,
} from "service/Authority/authority.js";

export default {
  data() {
    return {
      currentPage4: 1,
      formname,
      data: [],
      tableData: [],
      isNewDialogDisplay: false,
      isDeleteDialogDisplay: false,
      selectData: [],
      newDialogPositionName: "",
      form: [{}, {}, {}, {}, {}],
      frompost: null,
      treedata: [],
      id: 0,
      userQuery:{
        pageNumber:1,
        pageSize:8,
        positionName:""
      }
    };
  },
  created() {
    this.getAllAuthPosition(this.userQuery);
    this.getAllDeptForms();
    getAllAuthPositionFilter();
  },
  methods: {
    //筛选职位
    query() {
      this.getAllAuthPosition(this.userQuery);
    },
    //获取筛选职位
    getAllAuthPosition(data) {
      getAllAuthPosition(data).then((res) => {
        if (res.success) {
          this.tableData = res.result.items;
          this.$message.success("获取列表成功");
          console.log(this.tableData);
        } else {
          this.$$message.error("获取列表失败");
        }
      });
    },
    //获取部门信息
    getAllDeptForms() {
      getAllDeptForms().then((res) => {
        if (res.success) {
          this.data = getAllDeptFormsFilter(res.result.items);
        } else {
          this.$message.error("请重新刷新页面");
        }
      });
    },
    //编辑
    edit(scope) {
      console.log(scope);
      this.id = scope.row.id;
      this.formname = scope.row.form;
      var data;
      var arr;
      this.isNewDialogDisplay = true;
      this.newDialogPositionName = scope.row.positionName;
      getSingleAuthPosition(this.id).then((res) => {
        if (res.success){
          console.log(res.result.formManagers);
          this.$message.success("获取单个职位信息成功");
          data = res.result.formManagers;
          this.formname = data;
          arr = getSingleAuthPositionFilter(data);
          this.$nextTick(() => {
          this.$refs.tree.setCheckedKeys(arr);
          });
        } else {
          this.$message.error("服务器出错!");
        }
      });
    },
    rowclick(selection, row) {
      this.selectData = selection;
      console.log(this.selectData);
    },
    handleSizeChange(){
      console.log("aaaa")
    },
    handleCurrentChange(){
      this.query();
    },
    //新建职位
    newDialog(){
      this.isNewDialogDisplay = true;
      this.id = 0;
      this.newDialogPositionName = '';
      this.treedata = [];
      this.$nextTick(() => {
            this.$refs.tree.setCheckedKeys(this.treedata);
          });
    },
    //新建确认
    newDialogConfirm() {
      var selectData = this.$refs.tree.getCheckedNodes(true);
      console.log(selectData);
      var arr = this.formname;
      arr.forEach(item =>{
        item.allowQuery =false;
        item.allowModify = false;
      });
      var obj = new Object();
      console.log(arr);
      selectData.forEach((item) => {
        let i = parseInt(item.key / 2);
        let j = item.key % 2;
        if (j === 1) {
          arr[i].allowQuery = true;
        } else {
          arr[i].allowModify = true;
        }
      });
      obj.id = this.id;
      obj.positionName = this.newDialogPositionName;
      obj.formManagers = arr;
      if(this.id==0){
        InsertPosition(obj).then((res) => {
        if (res.success) {
          this.$message.success("添加成功");
          window.location.reload();
        } else {
          this.$message.error("添加失败");
        }
      });
      }
      else{
        UpdatePosition(obj).then((res) => {
        if (res.success) {
          this.$message.success("添加成功");
          window.location.reload();
        } else {
          this.$message.error("添加失败");
        }
      });
      }
      /*       console.log(this.formname); */
    },
    //删除事件
    removePreview() {
      if (this.selectData.length == 0) {
        this.$message.error("请选择职位");
      } else {
        this.isDeleteDialogDisplay = true;
      }
    },
    //确认删除
    remove(selection) {
      var arr = new Array(selection.length);
      console.log(selection);
      selection.forEach((item, index) => {
        arr[index] = item.id;
      });
      DeletePosition(arr);
      this.isDeleteDialogDisplay = false;
      var data = getAllAuthPositionProcess(1, 10);
      getAllAuthPosition(data).then((res) => {
        if (res.success) {
          this.tableData = res.result.items;
          this.$message.success("获取列表成功");
          console.log(this.tableData);
        } else {
          this.$message.error("获取列表失败");
        }
      });
       /* window.location.reload();  */
    },
  },
};
</script>

<style scoped>
.el-row{
  margin-bottom: 10px;
  line-height: 40px;
}
.el-input {
  width: 65% !important;
}
.new_row {
  height: 40px;
}
.el-tree {
  margin-top: 20px;
  margin-left: 15%;
  margin-bottom: 20px;
  min-height: 250px;
  text-align: left;
}
.delete > div {
  margin: 20px auto;
  min-height: 100px;
}
.delete > div > div:nth-child(1) {
  text-align: center;
}
.delete > div > div {
  display: inline-block;
  width: 50%;
  text-align: left;
  vertical-align: top;
  font-size: 18px !important;
}
.delete > div > div:nth-child(2) {
  max-height: 180px;
  overflow: auto;
}
.btn {
  width:100px
}
</style>